import { useState } from "react";
import AddItem from "./AddItem";
import ListItem from "./ListItem";
const maxLength = "5";

function List() {
  const [todo, setTodo] = useState([]);

  const handleAdd = (text) => {
    const newtodo = {
      id: Date.now(),
      title: text,
      status: false
    };

    setTodo([...todo, newtodo]);
  };
  const handleDelete = (id) => {
    const modifiedlist = todo.filter((elem) => elem.id !== id);
    setTodo(modifiedlist);
  };
  const handleToggle = (id) => {
    const modifiedlist = todo.map((elem) =>
      elem.id === id ? { ...todo, status: !todo.status } : todo
    );
    setTodo(modifiedlist);
  };
  return (
    <div>
      <AddItem handleAdd={handleAdd} />
      <ul>
        {todo.map((elem) => (
          <ListItem
            key={elem.id}
            handleDelete={() => handleDelete(elem.id)}
            handleToggle={() => handleToggle(elem.id)}
            title={elem.title}
            status={elem.status}
          />
        ))}
      </ul>
    </div>
  );
}

export default List;
